
.update-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.update-form-container {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.update-form {
    display: flex;
    flex-direction: column;
}

.update-form label {
    margin-bottom: 5px;
}

.update-form input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
}

.update-form button[type="submit"] {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.update-form button[type="submit"]:hover {
    background-color: #0056b3;
}

.go-home-link {
    display: block;
    margin-top: 20px;
    text-align: center;
}

.error-msg {
    color: #dc3545;
    margin-top: 10px;
    text-align: center;
}
