.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.user-list {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.user-list h2 {
    margin-bottom: 20px;
}

.user-list ul {
    list-style: none;
    padding: 0;
}

.user-list ul li {
    margin-bottom: 10px;
}

.user-list ul li a {
    text-decoration: none;
    color: #007bff;
}

.add-user-btn {
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 16px;
    margin-bottom: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.add-user-btn:hover {
    background-color: #218838;
}

.go-home-link {
    display: block;
    margin-top: 20px;
    text-align: center;
}

.error-msg {
    color: #dc3545;
    margin-top: 10px;
    text-align: center;
}