.user-profile-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.user-details {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.user-details h2 {
    margin-bottom: 20px;
}

.admin-options button {
    background-color: #dc3545;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 16px;
    margin-top: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.admin-options button:hover {
    background-color: #c82333;
}

.go-home-link {
    display: block;
    margin-top: 20px;
    text-align: center;
}

.error-msg {
    color: #dc3545;
    margin-top: 10px;
    text-align: center;
}