.login-container {
    display: flex;
    height: 100vh;
    overflow: hidden;
}

.sidebar {
    width: 250px;
    background-color: #252c48;
    color: #fff;
    padding: 20px;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
}

.logo {
    font-size: 24px;
    margin-bottom: 20px;
}

.links {
    margin-top: 40px;
}

.links a,
.links button {
    display: block;
    padding: 10px;
    margin-bottom: 10px;
    text-decoration: none;
    color: #fff;
    background-color: #31416b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.links a:hover,
.links button:hover {
    background-color: #46578c;
}

.content {
    flex: 1;
    padding: 20px;
}